import React, { Component } from 'react'
import PropTypes from 'prop-types';

export default class Todoing extends Component {
    // delItem(idx,e){
    //     this.props.del(idx);
    //     console.log(e);
    // }
    render() {
        var {todo} = this.props;
        return (
            <div>
                <ul>{
                    todo.map((item,idx) => {//map循环
                        // return <li key={idx}>{item}---<button onClick={this.delItem.bind(this,idx)}>删除</button></li>
                        return <li key={idx}>{item}---<button onClick={(e)=>this.props.del(idx,e)}>删除</button></li>
                    })
                }</ul>
            </div>
        )
    }
}
Todoing.propTypes = {//类型检查
    todo:PropTypes.array,
    del:PropTypes.func
}

Todoing.defaultProps = {//设置默认值
    todo:[1,2,3,4,5]
}