import React, { Component } from 'react'
import Todoinput from './Todoinput'
import Todoing from './Todoing'

export default class Todolist extends Component {
    constructor(){
        super();
        this.state = {
            todo:[1,2,3]
        }

        //深拷贝\浅拷贝
        var obj = {a:100,b:[1,2,3]};
        var obj1 = {c:300};
        // var o = {...obj};
        var o = Object.assign({},obj,obj1);
        // var o = JSON.parse(JSON.stringify(obj));
        // o.a = 200;
        // o.b[0] = 200;
        console.log(o === obj);
        // console.log(Object.keys(obj));//["a", "b"]
        Object.keys(obj).forEach((item)=>{
            console.log(item,obj[item]);
            // console.log(obj[item]);//变量用中括号访问
        })

    }
    addItem = (data)=>{
        // this.state.todo.push(data);
        // console.log(this.state.todo);
        this.setState({
            todo:[...this.state.todo,data]
        })
    }
    delItem = (idx,e)=>{
        //1、不能直接改变或处理state，通过setState改变
        console.log(idx,e);
        let todo = [...this.state.todo];
        todo.splice(idx,1);
        //2、setState是异步执行
        this.setState((state,props)=>{
            console.log(state.todo);
            return{
                todo:todo
            }
        })

        //在setState里获取state可能会出错，不是你想要的值
        //可以像上面那样，把第一个参数写成函数
        // this.setState({
        //     todo:this.state.num + 100
        // },()=>{
        //     console.log(this.state.todo);
        // })        
    }
    render() {
        return (
            <div>
                <Todoinput add={this.addItem}/>
                <Todoing del={this.delItem} todo={this.state.todo}/>
            </div>
        )
    }
}